<template>
  <div id="wallet">
    <common-header title="零钱" link="零钱明细" @linkClick="linkTo('WalletData')"></common-header>
    <div class="wallet__info">
      <div class="wallet__info__logo">
        <img src="../../../assets/wallet_logo.png" alt="">  
      </div>
      <div class="wallet__info__title">我的钱包</div>
      <div class="wallet__info__value">￥{{accounts.balanceTotal?accounts.balanceTotal:0}}</div>
    </div>

    <div class="wallet__btn">
      <div class="btn__recharge" @click="linkTo('Recharge')">充值</div>
      <div class="btn__withdraw" @click="linkTo('WithDraw')">提现</div>
    </div>
  </div>  
</template>

<script>
  export default {
    data() {
      return {
        accounts: {}
      }
    },
    
    created() {
      this.getMyAccountList();
    },

    methods: {
      getMyAccountList() {
        this.$Api.getMyAccountList(1).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
            this.accounts = res.q.Accounts[0];
          }
        })
      }
    }
  }
</script>


<style lang="scss">
  #wallet {
    .wallet__info {
      padding-top: 1.067rem;
      text-align: center;

      .wallet__info__logo {
        margin: 0 auto;
        width: 2.933rem;
        height: 2.933rem;
      }

      .wallet__info__title {
        padding: 0.4rem 0;
        font-size: 0.4267rem;
      }

      .wallet__info__value {
        font-size: 0.9333rem;
      }
    }

    .wallet__btn {
      padding:0 0.4rem;
      padding-top: 0.8rem;
      width: 100%;

      .btn__recharge,
      .btn__withdraw {
        margin-bottom: 0.4rem;
        width: 100%;
        height: 1.2rem;
        line-height: 1.1733rem;
        text-align: center;
        border-radius: 1.2rem;
      }

      .btn__recharge {
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
        border: 1px solid transparent;
      }

      .btn__withdraw {
        color: #de7fef;
        border: 1px solid #de7fef;
      }
    }
  }
</style>




